<script>
export default {
  name: "history_hongbao",
  data() {
    return {
        history_hongbao_data:[],
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    }
  },
  created(){
    // let user_id = "1111"
    // let api = "https://elm.cangdu.org/promotion/v2/users/" + user_id + "/expired_hongbaos"
    let api = "https://elm.cangdu.org/promotion/v2/users/1/expired_hongbaos?limit=20&offset=0"
    this.$http({
      type:"get",
      url:api
    }).then(res => {
      console.log(res)
      this.history_hongbao_data = res.data;
        let limitArr = [];
        for(let i = 0;i < res.data.length;i++){
            if(res.data[i].limit_map != undefined){
                limitArr.push(res.data[i].limit_map);
            }
        }
    })
  }
};
</script>
